import { g, keyframes, React } from "src/utils/view"

type Props = {
  children?: string
}

export default function Loading({ children }: Props) {
  return (
    <Container>
      <Spinner>/</Spinner>
      <Gap />
      {children || "Loading..."}
    </Container>
  )
}

const animation = keyframes`
  from 0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(calc(360deg*3))
  }
`

const Container = g("div")({
  display: "flex",
})
const Gap = g("div")({
  width: 10,
})

const Spinner = g("div")({
  flex: "0 0",
  animation: `${animation} 1s ease-in-out infinite`,
})
