import { g, React } from "src/utils/view"

import Layout from "../components/layout"
import SiteLink from "../components/link"
import Loading from "../components/Loading"
import { getSecuredObject } from "../utils/crypto"

type Props = {
  children: React.ReactNode
}

type State = {
  data: "loading" | "error" | any
}

export default class CV extends React.PureComponent<Props, State> {
  state: State = {
    data: "loading",
  }

  async componentDidMount() {
    try {
      const cv = await getSecuredObject("cv")
      this.setState({ data: cv })
    } catch (e) {
      this.setState({ data: "error" })
    }
  }

  render() {
    if (this.state.data === "loading") return <Loading />
    if (this.state.data === "error")
      return (
        <Layout>
          <div>
            Wrong key, man. Ask me for access to this resource at{" "}
            <SiteLink to="mailto:nicosommi@gmail.com">
              nicosommi@gmail.com
            </SiteLink>
            , is not so secret anyways
          </div>
        </Layout>
      )
    const { name } = this.state.data
    return (
      <Layout>
        <Container>{name}</Container>
      </Layout>
    )
  }
}

const Container = g("span")({})
