import Blowfish from "egoroof-blowfish"
import qs from "query-string"
// import encryptedObject from "../../key.js"

const encryptedObject = require("../../key.json")

/*
*  uses the query string param named "key" as the secret for decrypt
*/
export async function getSecuredObject(key: string) {
  const queryStringObject = qs.parse(document.location.search)
  const qsSecret = queryStringObject["key"]
  const bf = new Blowfish(qsSecret, Blowfish.MODE.ECB, Blowfish.PADDING.NULL)

  if (!qsSecret) throw new Error("Invalid secret key provided")
  const stringToDecrypt = encryptedObject[key]
  if (!stringToDecrypt) throw new Error("Unknown key on secured object")
  const rawToDecode = stringToDecrypt.split(",")
  const ia = new Int8Array(rawToDecode)
  let object = bf.decode(ia as any, Blowfish.TYPE.STRING)
  object = object.substr(0, object.length - 1)

  return JSON.parse(object)
}
